import {
  CHANGE_USER_NAME,
  DEC,
  GET_PRODUCTS,
  DELETE_PRODUCTS,
  INC,
  EDIT_PRODUCTS,
  SAVE_PRODUCTS,
  EDIT_PRICE,
  SAVE_PRICE,
  CANCEL_PRODUCT,
  CANCEL_PRICE,
} from "../actions/type";
export const initialProducts = {
  products: [],
  status: [],
  prices: [],
  priceStatus: [],
};
const reducer = (state, action) => {
  switch (action.type) {
    case GET_PRODUCTS:
      return {
        ...state,
        products: action.data,
        status: action.data.map((val, index) => {
          return {
            value: false,
            id: index + 1,
          };
        }),
        price: action.data.price,
        priceStatus: action.data.map((val, index) => {
          return {
            value: false,
            id: index + 1,
          };
        }),
      };
    case DELETE_PRODUCTS:
      const priceStat = state.priceStatus.filter((val) => val.id !== action.id);
      const stat = state.status.filter((val) => val.id !== action.id);
      const products = state.products.filter((val) => val.id !== action.id);

      return {
        ...state,
        products: products,
        priceStatus: priceStat,
        status: stat,
      };
    case EDIT_PRODUCTS:
      const status = state.status.map((val, index) => {
        if (val.id === action.id) {
          val.value = true;
        }
        return val;
      });

      // console.log(state);
      return {
        ...state,
        status: status,
      };
    case SAVE_PRODUCTS:
      // console.log(action.id);
      const updateProducts = state.products.filter((val, index) => {
        if (val.id === action.id) {
          val.name = action.input;
        }
        return val;
      });
      const newStatus = state.status.map((val, index) => {
        if (val.id === action.id) {
          val.value = false;
        }
        return val;
      });

      return {
        ...state,
        products: updateProducts,
        status: newStatus,
      };
    case EDIT_PRICE:
      const prices = state.priceStatus.map((val, index) => {
        if (val.id === action.id) {
          val.value = true;
        }
        return val;
      });

      return {
        ...state,
        priceStatus: prices,
      };
    case SAVE_PRICE:
      const updatePrices = state.products.filter((val, index) => {
        if (val.id === action.id) {
          val.price = action.input;
        }
        return val;
      });
      const priceStatus = state.priceStatus.map((val, index) => {
        if (val.id === action.id) {
          val.value = false;
        }
        return val;
      });

      return {
        ...state,
        price: updatePrices,
        priceStatus: priceStatus,
      };
    case CANCEL_PRODUCT:
      const cancel = state.status.map((val, index) => {
        if (val.id === action.id) {
          val.value = false;
        }
        return val;
      });
      return {
        ...state,
        status: cancel,
      };
    case CANCEL_PRICE:
      const cancelPrice = state.priceStatus.map((val, index) => {
        if (val.id === action.id) {
          val.value = false;
        }
        return val;
      });

      return {
        ...state,
        priceStatus: cancelPrice,
      };

    default:
      return state;
  }
};
export default reducer;
